import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const PDO = () => {
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>PDO threads</h1>
          <h2 style={{ marginTop: "20px", fontSize: "30px" }}> FAQ </h2>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are PDO threads and how does it work?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                PDO (Polydioxanone) threads are a minimally invasive cosmetic
                procedure aimed at lifting and tightening sagging skin tissues.
                They work by inducing collagen production in the treated area.
                Made of biodegradable material, PDO threads are inserted under
                the skin using fine needles. Once in place, they act as a
                support structure for the skin, promoting collagen synthesis and
                tissue rejuvenation. Over time, the threads dissolve, leaving
                behind newly formed collagen strands which help maintain the
                lifted and youthful appearance of the skin.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are the details of my PDO threads procedure?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Appointments for PDO threads are typically around an hour long.
                After the procedure, most patients can return to their everyday
                activities. PDO thread treatments are quick and involve minimal
                downtime, making it possible to fit the procedure into a busy
                schedule, even during a lunch break. The treatment provides a
                temporary solution for skin laxity and sagging, requiring less
                commitment than surgical alternatives.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>How long do PDO threads last?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                The effects of PDO threads can last anywhere from 6 months to 2
                years. The longevity of the results depends on the type of
                threads used, the area treated, and the individual's response to
                the treatment. As the threads dissolve, they stimulate collagen
                production, which helps maintain the lifting effect even after
                the threads have been absorbed.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>What can PDO threads treat?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                PDO threads are primarily used for facial rejuvenation. They are
                effective in lifting sagging skin, reducing the appearance of
                wrinkles, and improving skin texture. Common treatment areas
                include the cheeks, jawline, neck, eyebrows, and nasolabial
                folds. PDO threads can also be used to enhance facial contours
                and restore a more youthful appearance.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>
                How do I know if I'm qualified for PDO threads?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Ideal candidates for PDO threads are those experiencing mild to
                moderate skin laxity and who are seeking a non-surgical solution
                for skin lifting and tightening. The procedure is suitable for
                most adults in good health. However, it may not be recommended
                for individuals with certain medical conditions, severe skin
                laxity, or those with a history of poor wound healing. A
                consultation with a qualified medical provider is necessary to
                determine if PDO threads are an appropriate treatment for you.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default PDO;
