import "./index.css";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import * as React from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Divider from "@mui/material/Divider";

const Injectables = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };
  const style = {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    maxWidth: "100%",
    bgcolor: "#C49DBC",
    marginTop: "50px",
    marginRight: "30px",
    marginLeft: "30px",
  };

  const navigate = useNavigate();
  const redirectToBotox = () => {
    navigate("/Botox");
  };

  const redirectToDysport = () => {
    navigate("/Dysport");
  };

  const redirectToDermalFiller = () => {
    navigate("/DermalFiller");
  };

  const redirectToKybella = () => {
    navigate("/Kybella");
  };

  const redirectToPRP = () => {
    navigate("/PRP");
  };

  const redirectToHyal = () => {
    navigate("/Hyal");
  };

  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1 style={{ marginBottom: "40px" }}>Injectables</h1>
          <p1>
            Botox, Dysport, Dermal Filler, and Kybella are all injectable
            treatments that can help you achieve your aesthetic goals. Whether
            you are looking to smooth out fine lines and wrinkles, or add volume
            to your lips, cheeks, or jawline, we have the perfect treatment for
            you.
            <br />
          </p1>

          <p2>Click below to learn more about each treatment.</p2>
          <List
            sx={style}
            component="nav"
            aria-label="mailbox folders"
            className="List"
          >
            <ListItem>
              <Button
                onClick={redirectToBotox}
                className="no-border"
                style={{
                  maxWidth: "100%",
                  backgroundColor: "#C49DBC",
                  fontSize: "30px",
                }}
              >
                Botox
              </Button>
            </ListItem>
            <Divider />
            <ListItem>
              <Button
                onClick={redirectToDysport}
                className="no-border"
                style={{
                  maxWidth: "100%",
                  backgroundColor: "#C49DBC",
                  fontSize: "30px",
                }}
              >
                Dysport
              </Button>
            </ListItem>
            <Divider />
            <ListItem>
              <Button
                onClick={redirectToDermalFiller}
                className="no-border"
                style={{
                  maxWidth: "100%",
                  backgroundColor: "#C49DBC",
                  fontSize: "30px",
                }}
              >
                Dermal Filler
              </Button>
            </ListItem>
            <Divider />
            <ListItem>
              <Button
                onClick={redirectToKybella}
                className="no-border"
                style={{
                  maxWidth: "100%",
                  backgroundColor: "#C49DBC",
                  fontSize: "30px",
                }}
              >
                Kybella
              </Button>
            </ListItem>
            <Divider />
            <ListItem>
              <Button
                onClick={redirectToPRP}
                className="no-border"
                style={{
                  maxWidth: "100%",
                  backgroundColor: "#C49DBC",
                  fontSize: "30px",
                }}
              >
                PRP
              </Button>
            </ListItem>
            <Divider />
            <ListItem>
              <Button
                onClick={redirectToHyal}
                className="no-border"
                style={{
                  maxWidth: "100%",
                  backgroundColor: "#C49DBC",
                  fontSize: "30px",
                }}
              >
                Hyaluronidase
              </Button>
            </ListItem>
          </List>
          <h2
            style={{
              marginTop: "20px",
              display: "flex",
              justifyContent: "center",
            }}
          >
            Already know what you want? Book an appointment today!
          </h2>
          <Button
            onClick={redirectToLink}
            style={{
              backgroundColor: "#FFFDF9",
              color: "#323232",
              fontWeight: "500",
              width: "100%",
            }}
          >
            Book an Appointment
          </Button>

          <div className="bottom">
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default Injectables;
