import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const Mesotherapy = () => {
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>Mesotherapy</h1>
          <h2 style={{ marginTop: "20px", fontSize: "30px" }}> FAQ </h2>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>What is Mesotherapy and how does it work?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                A skin booster is a dermal treatment designed to hydrate the
                skin and improve its elasticity, firmness, and overall
                appearance by introducing substances like hyaluronic acid into
                the skin. Unlike traditional fillers that are used to fill
                specific wrinkles or plump certain areas like the lips, skin
                boosters work to improve the skin's overall quality. Benefits of
                a Skin Booster: Deep hydration, improved skin texture, increased
                luminosity, and stimulating collagen production
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are the details of my Mesotherapy procedure?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                A Mesotherapy skin booster procedure typically takes about 30 to
                60 minutes, depending on the size of the area being treated.
                This minimally invasive treatment involves a series of
                micro-injections of vitamins, enzymes, hormones, and plant
                extracts to rejuvenate and tighten the skin. Before the
                procedure, the targeted area is cleansed, and a numbing cream
                may be applied to minimize discomfort. The Mesotherapy solution
                is then injected into the mesodermal layer of the skin using a
                fine needle. This infusion of nourishing ingredients aims to
                improve skin elasticity, promote collagen production, and
                enhance the skin's overall appearance. Patients usually
                experience minimal discomfort during the procedure and can
                return to their daily activities shortly after. Some may notice
                temporary bruising or swelling at the injection sites, but these
                side effects typically subside quickly. Mesotherapy skin booster
                treatments are often done in a series, with patients undergoing
                multiple sessions spaced a few weeks apart for optimal results.
                The treatment is versatile and can be tailored to address
                various skin concerns such as fine lines, wrinkles, dullness,
                and dehydration. Post-treatment, patients are advised to avoid
                sun exposure and use sunscreen to protect the treated area.
                Following a consistent skincare routine and staying hydrated can
                also help enhance and prolong the results of the Mesotherapy
                skin booster treatment.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>How long does the skin booster last?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                The duration of results from a Mesotherapy skin booster
                treatment can vary depending on several factors, including the
                type of solution used, the area treated, and the individual's
                skin type and lifestyle. Typically, the effects of Mesotherapy
                can last anywhere from 12 to 18 months. Most patients see
                initial improvements soon after their treatment, with the full
                effects becoming more apparent after completing a series of
                sessions. To maintain the results, follow-up treatments are
                often recommended every few months. The longevity of Mesotherapy
                results can be influenced by factors such as age, skin
                condition, diet, and sun exposure. Adopting a healthy lifestyle,
                using appropriate skincare products, and protecting the skin
                from sun damage can help extend the effectiveness of the
                treatment.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>What can Mesotherapy treat?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Mesotherapy is a versatile treatment that can address a variety
                of skin and body concerns such as Anti-Aging, Pigmentation, Acne
                Scars, Hair Loss, and Stretch Marks. Mesotherapy can even
                improve lymphatic drainage and blood circulation, benefiting
                overall skin health.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>
                How do I know if I'm qualified for Mesotherapy?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Determining if you're a good candidate for Mesotherapy involves
                considering various factors related to your overall health, skin
                condition, and specific concerns you want to address. Consult
                with us to see if you're fit for Mesotherapy.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>When should I not get Mesotherapy?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Mesotherapy should be avoided under certain circumstances. It's
                not recommended for pregnant or breastfeeding women due to
                potential risks to the baby. Individuals with a history of
                allergic reactions to components used in Mesotherapy should also
                avoid this treatment. People with active skin infections, immune
                system disorders, or inflammatory conditions in the treatment
                area are not suitable candidates, as Mesotherapy could
                exacerbate these issues. Those with underlying health problems
                like cancer, blood clotting disorders, diabetes, or heart
                disease should refrain from Mesotherapy, as it could pose
                additional health risks. It's always crucial to consult with a
                healthcare professional before deciding on Mesotherapy to ensure
                it aligns safely with your overall health and medical history.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default Mesotherapy;
