import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const Dysport = () => {
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>Dysport</h1>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>What is Dysport and how does it work?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dysport is designed to address moderate to severe frown lines
                between the eyebrows by diminishing muscle activity. At your
                appointment, your provider will evaluate the area recommended
                for treatment and the quantity of Dysport units necessary.
                Generally, to treat frown lines, Dysport is administered through
                one injection that targets five specific points between and
                above the eyebrows. These injections temporarily halt the
                repetitive forehead movements that contribute to the formation
                of frown lines and wrinkles.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are the details of my Dysport procedure?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Before your Dysport procedure, it's important to know that the
                appointment usually doesn't exceed 30 minutes. It's advisable to
                avoid alcohol and abstain from any blood thinners for 24-48
                hours before and after the treatment. Following your Dysport
                procedure, you'll be pleased to know there is no mandatory
                downtime. You can return to your daily activities immediately
                after the treatment. However, it is recommended to avoid
                exercise and any activities that may cause facial flushing for
                at least four hours post-treatment.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>How long does Dysport last?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Results from Dysport are quick to appear, usually within 2-3
                days following the treatment. The duration of Dysport's effects
                varies based on several factors. Although individual experiences
                differ, you can generally expect to enjoy the results for about
                five months.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>What can Dysport Treat?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dysport has received FDA approval for the treatment of patients
                suffering from cervical dystonia, a condition characterized by
                abnormal head positioning and neck pain. In two clinical
                studies, Dysport has been effective in alleviating these
                symptoms, with the majority of patients not requiring further
                treatment for a period of 14-18 weeks.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>
                How do I know if I'm qualified for Dysport?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                If you're looking to address moderate to severe facial wrinkles
                in the forehead area, considering Dysport or a similar
                neurotoxin treatment might be suitable. However, it's important
                to note that men and women with a milk allergy, those on certain
                medications for muscle spasms, and pregnant individuals are not
                suitable candidates for Dysport.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>When should I not get Dysport?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dysport is not recommended for use in women who are pregnant or
                nursing.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default Dysport;
