import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const Botox = () => {
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>Botox</h1>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>What is Botox and how does it work?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                At your appointment, your provider will assess the areas to be
                treated and determine the required amount of Botox units. Then,
                using fine micro-needles, Botox will be carefully injected into
                the facial muscles. This neurotoxin works by temporarily
                weakening the muscles, leading to a smoothing of wrinkles.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>What is Baby Botox?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Baby Botox, sometimes referred to as mini Botox or Barely-There
                Botox, is gaining popularity as a cosmetic trend, particularly
                among younger individuals seeking to proactively delay early
                aging signs. This approach, while aimed at the same objectives
                as traditional Botox treatments, involves a lower dose of Botox
                for subtler results. If you're exploring Botox for the first
                time and desire a more natural appearance, Baby Botox could be
                an excellent choice. In your free consultation, your medical
                provider will suggest a tailored treatment plan that best suits
                your needs.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are the details of my Botox procedure?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Appointments are typically 15 minutes long, depending on the
                areas that are being treated. To reduce bruising, avoid
                anti-inflammatory supplements like flaxseed oil, fish oil, and
                vitamin E for 1 week prior to your appointment. You may take
                Tylenol if you are concerned about discomfort. Any bumps you see
                post procedure will last less than 30 minutes. Avoid exercising
                for 24 hours and try to leave your face alone.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>How long does Botox last?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Typically, Botox lasts 3-4 months. Some patients may experience
                a shorter duration of effect, while others may experience a
                longer duration of effect. The duration of effect is dependent
                on the patient's metabolism, the amount of Botox used, and the
                area being treated.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>What can Botox Treat?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Botox can be used to treat fine lines and wrinkles, including
                forehead lines, frown lines, and crow's feet. It can also be
                used to treat hyperhidrosis (excessive sweating) and masseter
                hypertrophy (enlarged jaw muscles).
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>How do I know if I'm qualified for Botox?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                If you're looking to address moderate to severe facial wrinkles,
                considering a neurotoxin treatment might be beneficial.
                Procedures involving injectables like Botox offer the
                flexibility to start with subtle improvements and gradually
                enhance the effects. Consulting with a skilled medical
                professional is crucial to assess your individual aesthetic
                objectives and decide on the most suitable neurotoxin treatment
                for you.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>When should I not get Botox?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Botox is not recommended for use in women who are pregnant or
                nursing.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default Botox;
