import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const IV = () => {
  return (
    <>
      <div>
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>IV Infusion Therapy</h1>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What is IV Infusion therapy and how does it work?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                IV infusion therapy is a medical treatment in which fluids,
                vitamins, minerals, and other nutrients are delivered directly
                into the bloodstream through an intravenous (IV) line. This
                method bypasses the digestive system, allowing for immediate and
                full absorption of the nutrients. IV infusion therapy is used
                for a variety of purposes, including:
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Hydration Therapy</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                This IV infusion is designed to quickly rehydrate your body at a
                cellular level, replenishing essential fluids and electrolytes.
                It's perfect for recovery from dehydration caused by activities
                like intense exercise, prolonged sun exposure, or illness.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Immune Boost Therapy</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Packed with vitamins, minerals, and antioxidants, this infusion
                aims to strengthen your immune system. It's ideal for those
                looking to boost their immunity, particularly during flu season
                or times of increased stress.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>Energy Boost Therapy</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                This energizing blend contains B-vitamins and other nutrients
                known for increasing energy levels. It's a great pick-me-up for
                individuals feeling fatigued or lethargic, helping to restore
                vitality and reduce feelings of tiredness.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>Detox IV Therapy</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Designed to help detoxify the body, this infusion includes
                compounds that aid in eliminating toxins and free radicals. It's
                beneficial for those seeking to cleanse their system or as
                support during a detox regimen.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>Inner Beauty</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Focused on skin health, this IV therapy combines hydrating
                fluids with a mix of vitamins and antioxidants that promote
                healthy, glowing skin. It's a popular choice for those looking
                to enhance their skin's appearance from the inside out.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>Myers Cocktail</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                A well-known and versatile infusion, the Myers Cocktail
                includes a blend of vitamins and minerals such as Vitamin C, B
                vitamins, and magnesium. Its used for a range of purposes, from
                boosting overall wellness to helping with conditions like
                migraines and chronic fatigue.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>NAD+ Therapy</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Utilizing Nicotinamide Adenine Dinucleotide (NAD+), this
                infusion is geared towards cellular repair and anti-aging. It's
                chosen by those interested in enhancing mental clarity, boosting
                metabolism, and supporting overall cellular health.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default IV;
