import * as React from "react";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const WLP = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>PRP</h1>
          <div>
            Platelet-Rich Plasma, is derived from your own blood. Platelets are
            a type of blood cell that plays a crucial role in healing processes.
            They release growth factors that stimulate cell regeneration,
            promote tissue repair, and encourage the formation of new blood
            vessels. When injected into specific areas of the body, PRP uses
            these natural processes to support healing and rejuvenation. PRP is
            commonly used for skin rejuvenation to improve skin texture, reduce
            wrinkles, and promote a more youthful appearance. Also, it is used
            for hair loss treatment. Injected into the scalp to stimulate hair
            growth and strengthen hair follicles. PRP treatments are popular for
            those seeking a natural approach to healing and rejuvenation using
            the body's own biological processes. This procedure will typically
            take 60 minutes.
          </div>

          <Button
            onClick={redirectToLink}
            style={{
              backgroundColor: "#FFFDF9",
              color: "#323232",
              fontWeight: "500",
              width: "100%",
            }}
          >
            Book an Appointment
          </Button>

          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default WLP;
