import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const DermalFiller = () => {
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>Dermal Filler</h1>
          <h2 style={{ marginTop: "20px", fontSize: "30px" }}> FAQ </h2>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are dermal fillers and how does it work?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dermal fillers are injectable substances made with hyaluronic
                acid, a natural compound in your body that helps keep your skin
                hydrated and plump. When injected into specific facial areas,
                these gel-like substances add volume beneath the skin,
                effectively reducing the appearance of fine lines and wrinkles.
                Besides smoothing the skin, dermal fillers can enhance facial
                symmetry, add volume to sunken cheeks and temples, contour the
                face, and fill acne scars. The process of a dermal filler
                treatment involves thoroughly cleaning the targeted area,
                followed by the application of a topical anesthetic if needed.
                Many dermal injectables include a local numbing agent to
                minimize discomfort during the procedure. You might feel a
                slight pinching or pressure as the filler is injected. The
                medical provider will then carefully inject a precise amount of
                filler under the skin in the targeted area. For optimal results,
                the use of medical-grade skincare products may be recommended
                following the treatment.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are the details of my dermal filler procedure?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Appointments for dermal fillers are usually an hour. After the
                treatment, patients are typically able to return to their
                regular daily activities. Dermal fillers offer a convenient and
                swift treatment with minimal downtime. It's feasible to schedule
                the procedure during a lunch break and then proceed with your
                day as usual. As a non-permanent alternative to surgical
                procedures for treating wrinkles and smoothing the skin, dermal
                fillers offer a less committed approach, requiring occasional
                maintenance.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>How long do dermal fillers last?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                The longevity of dermal fillers varies depending on the specific
                type of filler used and the area treated. Generally, the results
                can last from one to two years.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>What can dermal filers treat?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dermal fillers have received FDA approval for use in treating a
                broad range of facial areas. These include the lips, nasolabial
                folds (the creases running from the bottom of the nose to the
                mouth), marionette lines (lines extending from the corners of
                the mouth downwards), smokers' lines around the lips, the area
                under the eyes, cheeks, and jawline.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>
                How do I know if I'm qualified for dermal fillers?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dermal fillers offer a non-invasive option for facial
                augmentation, suitable for both men and women. This treatment
                allows for starting with subtle enhancements, which can be
                increased over time if desired, presenting a low-commitment
                approach to aesthetic improvement. They are particularly
                effective for patients with static wrinkles and lines, reduced
                facial volume, acne scars, asymmetrical features, or those
                seeking facial contouring. However, it's important to note that
                dermal fillers have not been extensively researched in women who
                are pregnant or breastfeeding, nor in individuals under the age
                of 18.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>
                How do I know which dermal filler is right for me?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Dermal filler treatments are personalized and tailored to meet
                the unique needs of each individual. Given the wide variety of
                dermal treatment options available, choosing the most suitable
                one for your specific requirements can be daunting. Therefore,
                it's crucial to collaborate with a skilled medical provider who
                can thoroughly evaluate your aesthetic goals and guide you in
                selecting the appropriate treatment.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default DermalFiller;
