import * as React from "react";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const WLP = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/PDO" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Mesotherapy" className="dropdown">
                    Mesotherapy
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">
                  <Link to="/HR" className="dropdown">
                    Hair Restoration Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="5">
                  <Link to="/WLP" className="dropdown">
                    Weight Loss Program
                  </Link>
                </DropdownItem>
                <DropdownItem key="6">
                  <Link to="/IV" className="dropdown">
                    IV Infusion Therapy
                  </Link>
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>Hyaluronidase</h1>
          <div>
            Hyaluronidase is an enzyme that has the unique ability to break down
            hyaluronic acid, a substance commonly found in the body and widely
            used in dermal fillers. This enzyme is particularly useful in
            aesthetic and medical treatments for several reasons. Hyaluronidase
            is used primarily to dissolve hyaluronic acid-based fillers,
            allowing for adjustments or corrections of dermal filler procedures.
            It can also enhance the absorption and dispersion of other injected
            drugs or fluids in the body. In medical scenarios, hyaluronidase is
            beneficial for promoting the spread of local anesthetics, increasing
            the efficacy of other treatments, or reducing tissue swelling.
          </div>

          <div>
            Some of the many benefits of Hyaluronidase are: Correcting dermal
            filler mistakes, reducing complications such as obstructed blood
            flow, and improving absorption and dispersion of other drugs,
            makings treatment more effective.
          </div>

          <Button
            onClick={redirectToLink}
            style={{
              backgroundColor: "#FFFDF9",
              color: "#323232",
              fontWeight: "500",
              width: "100%",
            }}
          >
            Book an Appointment
          </Button>

          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default WLP;
