import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const Kybella = () => {
  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            {/*<a href="#" className="item">
              About
            </a>*/}
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/Injectables" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">
                  <Link to="/Page" className="dropdown">
                    IV Infusion
                  </Link>
                </DropdownItem>
                <DropdownItem key="3">
                  <Link to="/Clone" className="dropdown">
                    PDO Threads
                  </Link>
                </DropdownItem>
                <DropdownItem key="4">Weight Loss Program</DropdownItem>
                <DropdownItem key="5">Microneedling</DropdownItem>
                <DropdownItem key="6">Hair Restoration</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          <h1>Kybella</h1>
          <h2 style={{ marginTop: "20px", fontSize: "30px" }}> FAQ </h2>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>What is Kybella and how does it work?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Kybella is an effective treatment for reducing moderate to
                severe fat pockets beneath the chin. It involves injecting a
                synthetic form of deoxycholic acid into the targeted area.
                Deoxycholic acid is a molecule that naturally occurs in the body
                and assists in breaking down and absorbing dietary fat. When
                injected into areas with unwanted submental fullness, this
                substance works by destroying fat cells, preventing them from
                storing fat in the future. The process is non-invasive,
                requiring no incisions.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>
                What are the details of my Kybella procedure?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                During a Kybella treatment, your medical provider will mark
                several injection sites under your chin. To minimize discomfort,
                a topical anesthetic is applied before the injections. The
                number of injections depends on the amount of fat under your
                chin and your desired outcome. Once the fat cells are destroyed
                by the treatment, they can no longer store or accumulate fat.
                These cells are then naturally processed and eliminated by your
                body's metabolism, typically without any pain or discomfort. The
                downtime following a Kybella treatment is generally minimal,
                especially after the initial session, with fewer side effects
                noted in subsequent treatments. You can resume normal activities
                right after your appointment. The deoxycholic acid in Kybella
                continues to work on breaking down fat deposits post-treatment,
                again usually without causing any discomfort. The number of
                Kybella sessions you'll need varies based on the volume and
                distribution of your submental fat, as well as your personal
                treatment objectives. Visible improvements are often noticed
                after two to four sessions, with a maximum of six sessions,
                spaced at least one month apart, sometimes necessary. The
                results from Kybella are not immediate; they typically become
                noticeable over a period of three to four months following the
                first session. This gradual change occurs as your body
                eliminates the treated fat cells through the lymphatic system.
                In clinical studies, a significant majority of patients reported
                a marked improvement in their appearance following Kybella
                treatment for submental fullness. Kybella appointments are
                fairly quick and only require about 30 minutes to complete.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography>How long does Kybella last?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                After achieving your desired aesthetic results with Kybella,
                there's typically no need for re-treatment of the area, provided
                you maintain a healthy weight. Kybella works by permanently
                destroying subcutaneous fat cells, which means that fat is less
                likely to accumulate in the treated area again.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>What can Kybella treat?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Kybella is exclusively FDA-approved for the treatment and
                contouring of submental fat, which is the fat under the chin, in
                adults.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion className="Accordion">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3a-content"
              id="panel3a-header"
            >
              <Typography>
                How do I know if I'm qualified for Kybella?
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Kybella is suitable for both men and women who are looking to
                reduce fat under their chin and enhance their chin profile
                without resorting to surgical methods. However, it's important
                to note that Kybella may not be the best option for everyone.
                Specifically, patients with prominent platysmal bands (the
                muscles that start at the base of the neck and extend up through
                the jaw) or those who have a significant amount of loose skin
                might not achieve the best results with Kybella treatments.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <div className="bottom" style={{ marginTop: "200px" }}>
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default Kybella;
